import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';

import { WatchComponent } from './vid/watch/watch.component';
import { TagsComponent } from './vid/tags/tags.component';
import { TagComponent } from './vid/tag/tag.component';
import { ExploreComponent } from './vid/explore/explore.component';

import { NointernetComponent } from './tech/nointernet/nointernet.component';

const app_routing: Routes = [
	{ path: 'Watch/:youtube_id', component: WatchComponent },
	{ path: 'Tags', component: TagsComponent },
	{ path: 'Tag/:tag_id', component: TagComponent },
	{ path: 'Nointernet', component: NointernetComponent },
	{ path: '', component: ExploreComponent }
];

@NgModule({
  imports: [
	CommonModule,
	RouterModule.forRoot(app_routing)
  ],
  exports: [
	RouterModule
  ],
  declarations: []
})
export class RoutingModule { }



