import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { WatchComponent } from './watch/watch.component';
import { TagsComponent } from './tags/tags.component';
import { TagComponent } from './tag/tag.component';
import { ExploreComponent } from './explore/explore.component';
import { VidService } from './vid.service';
import { RoutingModule } from '../routing.module';

@NgModule({
  imports: [ CommonModule, BrowserModule, HttpClientModule, RoutingModule ],
  providers: [ VidService ],
  declarations: [WatchComponent, TagsComponent, TagComponent, ExploreComponent]
})
export class VidModule {}


