import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Injectable()
export class VidService {
	public tag;
	public tags;
	public vids;
	private select_tag;
	constructor(private http: HttpClient, private router: Router) {
	 	http.get<any[]>('https://webart.work/api/user/pre/youkids').subscribe(resp => {
		 	this.tags = [];
		 	this.vids = [];
			if(router.url.indexOf('/Tag/')==0){
				this.select_tag = router.url.replace('/Tag/','');
			}
			for (let i = 0; i < resp.length; i++) {
				let tag = {
					_id: resp[i]._id,
					name: resp[i].name,
					vids: [],
					rand: null
				};
				for (let j = 0; j < resp[i].prefixes.length; j++) {
					let link = resp[i].data&&resp[i].data[resp[i].prefixes[j]];
					link = link&&link.split('v=')[1]||link&&link.split('v=')[0];
					link = link&&link.split('&')[0];
					if(link&&link.length==11){
						let vid = {
							name: resp[i].prefixes[j],
							link: link
						}
						this.vids.push(vid);
						tag.vids.push(vid);
					}
				}
				if(tag.vids.length>0){
					tag.rand = Math.floor(Math.random() * tag.vids.length);
					this.tags.push(tag);
					if(this.select_tag&&tag._id==this.select_tag){
						this.tag = tag;
					}
				}
			}
			if(this.select_tag&&!this.tag) router.navigate(['/Tags']);
		}, err => {
			router.navigate(['/Nointernet']);
		});
	}
}



